<template>
  <div>
    <h1 class="title">Выберите сложность</h1>
    <div class="difficulty">
      <div class="difficulty-item">
        <input
          id="radio-1"
          type="radio"
          class="radio-custom"
          value="easy"
          name="difficulty"
          v-model="difficulty"
          v-on:change="changeDifficulty"
        />
        <label for="radio-1" class="radio-custom-label">Легкий</label>
      </div>
      <div class="difficulty-item">
        <input
          id="radio-2"
          type="radio"
          class="radio-custom"
          value="middle"
          name="difficulty"
          v-model="difficulty"
          v-on:change="changeDifficulty"
        />
        <label for="radio-2" class="radio-custom-label">Средний</label>
      </div>
      <div class="difficulty-item">
        <input
          id="radio-3"
          type="radio"
          class="radio-custom"
          value="hard"
          name="difficulty"
          v-model="difficulty"
          v-on:change="changeDifficulty"
        />
        <label for="radio-3" class="radio-custom-label">Сложный</label>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "game-timer",
  data() {
    return {
      difficulty: "easy",
    };
  },
  methods: {
    changeDifficulty() {
      switch (this.difficulty) {
        case "easy":
          this.$store.commit("changeDifficulty", 1500);
          break;
        case "middle":
          this.$store.commit("changeDifficulty", 1000);
          break;
        case "hard":
          this.$store.commit("changeDifficulty", 400);
          break;
      }
    },
  },
};
</script>

